



















export default {
	data() {
		return {
			tel:'',
			pass:''
		};
	},
	methods:{
		getInfo(e,name){
			var that = this;
			if(name == 'tel'){
				that.tel = e.detail.value;
			}else if(name == 'pass'){
				that.pass = e.detail.value;
			}
		},
		login(){
			var that = this;
			if(!(/^1[34578]\d{9}$/.test(that.tel))){
				uni.showToast({
					title:'手机号格式不正确！',
					icon:'none'
				})
			}else if(that.pass.length == 0){
				uni.showToast({
					title:'密码不能为空！',
					icon:'none'
				})
			}else{
				uni.request({
					url:that.http+'user/login',
					header:that.header,
					method:'POST',
					data:{
						tel:that.tel,
						pass:that.pass
					},
					success: (res) => {
						if(res.data.code==200){
							uni.showToast({
								title:'登录成功！',
								icon:'none'
							})
							uni.setStorageSync('token',res.data.data.user_id);
							uni.navigateTo({
								url:'../../index/index'
							})
						}else{
							uni.showToast({
								title:res.data.mes,
								icon:'none'
							})
						}
					},
					fail() {
						uni.showToast({
							title:'网络延迟，请刷新后重试!',
							icon:'none'
						})
					}
				})
			}
		}
	}
}
